import {
  iconChevronLeft, iconChevronRight, iconEdit, loading,
} from './icons';

const line = '<hr class="mb-5 border-[#A7E6FF] w-11/12 mx-auto">';

const loadPage = `
  <div class="absolute inset-0 flex justify-center items-center bg-white backdrop-blur-sm bg-opacity-10 z-50">${loading}<div>
`;

const inputWIconLeading = (id, label, name, placeholder, icon, type = 'text') => `
  <div class="mb-5" id="${id}">
    <label for="${name}">${label}</label>
    <div class="relative mt-3 rounded-md shadow-sm">
      <div class="pointer-events-none absolute inset-y-0 left-0 flex items-center pl-3">${icon}</div>
      <input type="${type}" name="${name}" id="${name}" class="block w-full rounded-lg border-0 h-12 pl-12 pr-5 text-sm text-[#3572EF] ring-1 ring-inset ring-[#3572EF] placeholder:text-[#3572EF] focus:ring-2 focus:outline-0" placeholder="${placeholder}">
    </div>
  </div>
`;

const inputWIconTrailingCircle = (id, name, placeholder, icon) => `
  <div class="relative mb-3 rounded-full shadow-sm" id="${id}">
    <div id="search-btn" class="absolute inset-y-0 right-0 flex items-center pr-3 cursor-pointer">${icon}</div>
    <input type="text" name="${name}" id="${name}" class="block w-full rounded-full border-0 h-11 pl-5 pr-12 text-sm text-[#3572EF] ring-1 ring-inset ring-[#3572EF] placeholder:text-[#3572EF] focus:ring-2 focus:outline-0" placeholder="${placeholder}">
  </div>
`;

const inputWNoIcon = (id, label, name, placeholder, type = 'text', style = '') => `
  <div id="${id}" class="w-full mb-3">
    <label for="${name}">${label}</label>
    <input type="${type}" name="${name}" id="${name}" placeholder="${placeholder}"
      class="${style} block w-full rounded-lg border-0 h-11 px-5 mt-1 text-sm text-[#3572EF] ring-1 ring-inset ring-[#3572EF] placeholder:text-[#3572EF] focus:ring-2 focus:outline-0">
  </div>
`;

const buttonFilled = (id, text, style = '', href = '') => `
  <button id="${id}" class="bg-[#050C9C] text-white rounded-full w-full h-11 ${style}" onclick="location.assign('/#/${href}');">${text}</button>
`;

const buttonOutlined = (id, text, style = '', href = '') => `
  <button id="${id}" class="bg-white text-[#050C9C] border border-[#050C9C] rounded-full w-full h-11 ${style}" onclick="location.assign('/#/${href}');">${text}</button>
`;

const buttonText = (id, text, style = '', href = '') => `
  <button id="${id}" class="text-[#050C9C] w-full h-11 ${style}" onclick="location.assign('/#/${href}');">${text}</button>
`;

const textInfoOneLine = (title, value) => `
  <div class="flex justify-between mb-5">
    <p class="text-sm">${title}</p>
    <p class="text-sm">${value}</p>
  </div>
`;

const textInfoBoldOneLine = (title, value) => `
  <div class="flex justify-between mb-5">
    <p class="text-sm">${title}</p>
    <p class="text-sm font-bold">${value}</p>
  </div>
`;

const itemMenu = ({
  id, href, icon, count, unit, title, isNav,
}) => `
  <div id="${id}" class="w-full md:w-5/12 flex justify-between items-center grow p-4 shadow-md rounded-md cursor-pointer"
      onclick="location.assign('${href}');">
    <div class="flex items-center gap-6">
      <span>${icon}</span>
      <div class="flex flex-col justify-between">
        <p><span class="text-3xl font-bold">${count}</span> ${unit}</p>
        <p>${title}</p>
      </div>
    </div>
    ${isNav ? iconChevronRight : ''}
  </div>
`;

const itemClass = ({
  id, kelas, jurusan,
}) => `
  <div id="${id}" class="w-1/4 p-2 text-center grow bg-slate-100 shadow-md rounded-xl cursor-pointer"
      onclick="location.assign('/#/classroom'); localStorage.setItem('CURRENT_ID_SUBJECT', '${id}');">
    <p class="text-2xl font-bold">${kelas}</p>
    <p>${jurusan}</p>
  </div>
`;

const itemSubject = ({
  id, subject, schedules,
}) => {
  let listSchedule = '';
  schedules.forEach((schedule) => {
    const { day, startTime, endTime } = schedule;
    const days = ['Senin', 'Selasa', 'Rabu', 'Kamis', "Jum'at", 'Sabtu'];
    if (day > 0) {
      listSchedule += `<li class="mb-1">${days[day - 1]}, ${startTime}-${endTime}</li>`;
    }
  });
  return `
    <div id="${id}" class="w-full p-4 shadow-md rounded-lg border-s-8 border-[#A7E6FF]">
      <div class="flex justify-between items-center mb-4">
        <h3 class="text-lg font-bold underline cursor-pointer py-1.5"
          onclick="location.assign('/#/absent'); localStorage.setItem('CURRENT_TITLE_SUBJECT', '${subject}');">
          ${subject}
        </h3>
        <button id="btn-subject-${id}" class="flex items-center gap-1 text-sm p-1.5 px-4 rounded-md shadow-md"
          onclick="location.assign('/#/subject-form');">
          <span>${iconEdit}</span>
        </button>
      </div>
      <div class="flex justify-between">
        <p>Jadwal</p>
        <ul>${listSchedule}</ul>
      </div>
    </div>
  `;
};

