const itemCaseNote = ({
  id, name, date, notes,
}) => `
  <div id="${id}" class="w-full shadow-md rounded-md">
    <div class="bg-[#A7E6FF] px-4 py-1.5 rounded-lg">
      <h3 class="text-lg font-bold text-center cursor-pointer"
        onclick="location.assign('/#/');">
        ${name}
      </h3>
    </div>
    <div class="p-4">
      <p class="text-sm">${date}</p>
      <p class="mt-2">${notes}</p>
    </div>
  </div>
`;

const itemStudent = ({
  id, name, classroom, major, advisor, totalCase,
}) => `
  <div id="${id}" class="w-full p-3 mb-1 rounded-xl border-b-4 border-[#3572EF]">
    <div class="flex justify-between mb-1">
      <h5 class="font-bold">${name}</h5>
      <p>${classroom} / ${major}</p>
    </div>
    <div class="flex justify-between">
      <p>${advisor}</p>
      <p class="text-sm ${totalCase > 0 ? '' : 'text-[#A7E6FF]'}">${totalCase} Kasus</p>
    </div>
  </div>
`;

const appBar = (title, href = '') => `
  <div class="flex items-center mb-5">
    <button class="pe-5" onclick="location.assign('/#/${href}');">${iconChevronLeft}</button>
    <h1 class="text-xl">${title}</h1>
  </div>
`;

export {
  line,
  loadPage,
  inputWIconLeading,
  inputWIconTrailingCircle,
  inputWNoIcon,
  buttonFilled,
  buttonOutlined,
  buttonText,
  textInfoOneLine,
  textInfoBoldOneLine,
  itemMenu,
  itemClass,
  itemSubject,
  itemCaseNote,
  itemStudent,
  appBar,
};
